import { Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { AuthURL } from '../urls/rbac-url.config';
import { AlertService } from './alert.service';
import { HttpService } from './http.service';

@Injectable(
{
  providedIn: 'root'
}
)
export class AuthService {
  sharedInfo: any;
  alertService: AlertService;
  constructor(
    injector: Injector,
    private httpService: HttpService,
    private _router: Router
  ) {
    this.alertService = injector.get<AlertService>(AlertService);
  }

  public orgInfo = new BehaviorSubject<any>('');
  currentOrgInfo = this.orgInfo.asObservable();

  feedOrgInfo(data: any) {
    this.orgInfo.next(data);
  }
  public logout() {
    this._router.navigate(['/login']);
    sessionStorage.clear();
    localStorage.clear();
  }


  ResetPassword(data: any) {
    return this.httpService.post(AuthURL.EndPoints.auth.user.resetPassword, data);
  }
}
